}

@keyframes moveCubeScrollUp {
  0% {
    transform: translateZ(0);
  }

  100% {
    transform: translateY(100vh)translateZ(100vh);
  }
}

@keyframes moveCubeScrollDown {
  0% {
    transform: translateZ(0);
  }

  100% {
    transform: translateZ(-100vh);
  }
}

@keyframes rotateCubeScrollUp {
  0% {
    transform: translateY(-100vh)rotateX(0)rotateY(0)rotateZ(0);
  }

  100% {
    transform: rotateX(-90deg)rotateY(0)rotateZ(0);
  }
}

@keyframes rotateCubeScrollDown {
  0% {
    transform: rotateX(0)rotateY(0)rotateZ(0);
  }

  100% {
    transform: rotateX(90deg)rotateY(0)rotateZ(0);
  }
}

